@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Source+Code+Pro:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,900;1,200;1,300;1,400;1,500;1,600;1,700;1,900&display=swap);
.acerca-de {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px;
}

hr {
  width: 100%;
  color: #333;
}

.estilo-1 {
  width: 50%;
  height: 0;
  padding-bottom: 45%;
  position: relative;
}

.estilo-2 {
  position: absolute;
}

.footer {
  text-align: center;
  background-color: #333;
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* font-size: calc(10px + 2vmin); */
  color: white;
}

.footer a {
  color: azure;
  padding: 5px;
  margin: 0;
  font-size: 85%;
  /* background-color: rgba(27, 31, 35, 0.05); */
  border-radius: 3px;
  border: 1px solid azure;
  text-decoration: none;
}

footer a:hover {
  transition: 0.3s;
  background-color: azure;
  color: #333;
}

.navbar {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  height: 6vh;
  background-color: #333;
  padding: 5px;
}

.links a {
  color: white;
  text-decoration: none;
  border: 1px solid white;
  border-radius: 3px;
  padding: 5px;
  margin: 0 5px;
}

.links :hover {
  background-color: white;
  color: #333;
  transition: 0.3s;
}

.title {
  color: white;
  font-size: 1.2em;
  font-weight: bolder;
  text-decoration: none;
  padding: 3px;
}

.links {
  display: flex;
}

.active-link {
  background-color: gray;
}

.presentacion {
  text-align: center;
  background-color: #282c34;
  min-height: 75vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.primer-programa {
  padding: 15px;
}

.grafica {
  display: flex;
  justify-content: center;
}

.segundo-programa {
  padding: 15px;
}

.segundo-programa button {
  border: 1px solid #333;
  border-radius: 5px;
  margin: 0px 10px;
  font-family: "Montserrat";
  background-color: white;
  font-size: 1em;
  padding: 5px;
}

.segundo-programa button:hover {
  background-color: #333;
  animation: 0.5s;
  color: azure;
}

#grafica-programa-2 {
  display: flex;
  justify-content: center;
}

.titlo-grafica {
  text-align: center;
}

.label-eje {
  color: #fff;
  font-size: 1.5em;
  font-family: serif;
}

.label-eje-numero {
  color: #fff;
  font-size: 0.8em;
  font-family: serif;
}

.label {
  color: #fff;
  margin-top: -1em;
  font-family: sans-serif;
  /* font-size: 2em; */
  padding: 2px;
  background: rgba(0, 0, 0, 0.6);
}

.app {
  display: flex;
  flex-direction: column;
}

.container {
  min-height: 80vh;
}

